import {
	uniList,
	uniListItem,
	uniNavBar,
	uniIcons
} from '@dcloudio/uni-ui';
let delayTimer;
export default {
	components: {
		uniList,
		uniListItem,
		uniNavBar,
		uniIcons
	},
	onLoad() {
		this.getkeys();
	},
	data() {
		return {
			valueInput: "",
			keys: [],
			search_keys: [],
			list: [],
			listItem: [{},
				{
					title: "标题文字",
					note: "备注说明",
					amount: "123",
					datetime: "今天12:21",
					thumb: 'https://img-cdn-qiniu.dcloud.net.cn/new-page/hx.png'
				}, {
					title: "标题文字",
					note: "备注说明",
					amount: "123",
					datetime: "今天12:21",
					thumb: 'https://img-cdn-qiniu.dcloud.net.cn/new-page/hx.png'
				}
			]

		}
	},
	methods: {
		//返回上一页面
		toback() {
			uni.navigateBack({
				delta: 1
			});
		},
		// 监听输入框 
		inputValue(e) {
			console.log(e.target.value)
			let _value = e.target.value;
			let i = 0;
			if (delayTimer) {
				clearInterval(delayTimer);
			}
			delayTimer = setInterval(() => {
				i++;
				if (i === 30) {
					if (_value) {
						this.valueInput = _value;
						this.keys.push(this.valueInput);
						let txt = this.keys.join(',')
						clearInterval(delayTimer);
						try {
							uni.setStorageSync('search_key', txt);
						} catch (e) {
							// error
						}
						this.search(_value);
					}
				}
			}, 50);
		},
		search(_value) {
			console.log(this.valueInput)
			this.list = this.listItem
			uniCloud.callFunction({
				name: 'get',
				data: {
					useTypeLabel: _value
				}
			}).then((res) => {
				uni.hideLoading()
				console.log('res:',res)
			}).catch((err) => {
				uni.hideLoading()
				uni.showModal({
					content: `查询失败，错误信息为：${err.message}`,
					showCancel: false
				})
			})
		},
		
		// 获取搜记录
		getkeys() {
			try {
				const value = uni.getStorageSync('search_key');
				if (value) {
					this.keys = value.split(',');
				}
			} catch (e) {
				// error
			}
		},
		// 选择某个记录搜索
		handlesey(item) {
			console.log("handlesey:", item)
			this.valueInput = item;
			this.search();
		},
		// 删除某条记录
		handledel(item, index) {
			this.keys.splice(index, 1)
			let txt = this.keys.join(',')
			try {
				uni.setStorageSync('search_key', txt);
			} catch (e) {
				// error
			}
		},
		// 清除所有
		clearAll() {
			this.keys = [];
			try {
				uni.setStorageSync('search_key', '');
			} catch (e) {
				// error
			}
		},
		//   点击某条数据查看详情
		handleItem(item) {
			let _item = JSON.stringify(item)
			uni.navigateTo({
				url: './details?item=' + _item
			});
		},
	},
	created() {}
}
